import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Alert, Col, Row } from 'reactstrap';
import {
  CellEditProps,
  cepFormatter,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OperationEnum,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CepLocalidade, CLASS_NAME } from '../../../../models/dtos/ger/cepLocalidade';
import { CepUF } from '../../../../models/dtos/ger/cepUF';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigoMunicipio',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CepLocalidadeSearchView: FC<SearchViewProps<CepLocalidade>> = props => {
  const [uf, setUf] = useState<CepUF>(null);
  const [expressions, setExpressions] = useState<SearchExpression[]>(undefined);
  const formProps = useForm<CepLocalidade>({ mode: 'onBlur' });

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigoMunicipio',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código do Município',
    },
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome',
    },
    {
      dataField: 'uf.sigla',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Sigla da UF',
    },
    {
      dataField: 'cep',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: cepFormatter,
      sort: true,
      text: 'CEP',
    },
  ];

  function getExpressions() {
    return [
      {
        expressions: [
          {
            fieldName: 'uf.sigla',
            operation: OperationEnum.EQUAL,
            value: uf.sigla,
          },
        ],
        fieldName: 'uf.sigla',
        value: uf.sigla,
      },
    ];
  }

  useEffect(
    () => {
      if (uf) {
        setExpressions(getExpressions());
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [uf]
  );

  const onChangeUf = value => setUf(value);

  return (
    <WindowComponent title="Manutenção de Cidade">
      <Row>
        <Col>
          <Alert color="primary" className="text-center">
            Selecione a UF para efetuar a consulta
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS formProps={formProps} id="uf" isPrependLabel={true} onChange={onChangeUf} />
        </Col>
      </Row>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'CepLocalidadeEditView', CLASS_NAME)}
        searchExpressionsProps={expressions}
        remote={{
          cellEdit: false,
          filter: false,
          pagination: false,
          search: false,
          sort: false,
        }}
      />
    </WindowComponent>
  );
};

export default CepLocalidadeSearchView;
